import React from 'react'
import Slider from "react-slick"
import "slick-carousel/slick/slick.css"
import "slick-carousel/slick/slick-theme.css"
import styled from 'styled-components'

import prevButton from '../../image/prevButton.png'
import nextButton from '../../image/nextButton.png'

import { Projects } from '../../data'
import Box from '../atoms/ProjectBox'

const Prev = styled.img`
width: 50px;
height: 50px;
z-index: 5;
margin-left: -40px;
margin-top: -30px;
`

const Next = styled.img`
width: 50px;
height: 50px;
z-index: 5;
margin-right: -40px;
margin-top: -30px;
`

function ProjectSlider() {

    const { list } = Projects();
    
    
    const PrevArrow=({currentSlide,slideCount,...props})=>(
        <Prev {...props} src={prevButton} className="slick-prev" /> 
    )

    const NextArrow=({currentSlide,slideCount,...props})=>(
        <Next {...props} src={nextButton} className="slick-next" /> 
    )

    const settings = {
        dots: true, //      .
        infinite: true, // loop ( - - - )
        speed: 500, //  ,  milliseconds
        slidesToShow: 2, //     
        slidesToScroll: 2, //     
        nextArrow: <NextArrow />,
        prevArrow: <PrevArrow />,	
      };

    return (
        <Slider {...settings}>
            
                {list.map((project) => {             
                    return (<div>
                    <Box key={project.id} item={project} />
                    </div>)
                })}
        </Slider>
    )
}

export default ProjectSlider
